import React, { useState } from "react";
import { Button, Col, Form, Row } from "react-bootstrap";
import "./formRate.scss";


const FormRate = ({ onChange }) => {
  const [formData, setFormData] = useState({
    review: ""
  });

  const handleChange = (event) => {
    const { name, value} = event.target;
  

  
    setFormData({
      ...formData,
      [name]: value,
    });
    onChange(formData)
  };


  return (
    <div className="containterRate">
      <Form noValidate >
        <Row className="mb-3">
          <Form.Group as={Col} xs="12" sm="12" controlId="validationCustom01">
            <Form.Control
              style={{ width: "20rem", padding: "10px", fontSize: "1rem" }}
              className={"campo"}
              as="textarea"
              required
              type="text"
              placeholder=""
              name="review"
              value={formData.review}
              onChange={handleChange}
                        />
          </Form.Group>
        </Row>


    

      </Form>

    </div>
  );
};

export default FormRate;
