import React, { useState } from "react";
import ImageCarousel from "../../../components/carousel/imageCarousel.tsx";
import { Header } from "../../../components/header/header.tsx";
import { Button, Row } from "react-bootstrap";
import Rate from "../../../components/rate/rate.tsx";
import FormRate from "../../../components/form/formRate/formRate.tsx";
import { createReview } from "./review.service.tsx";
import { Loading } from "../../../components/loading/loading.tsx";
import "./review.scss";
import { Menu } from "../../../components/menu/menu.tsx";

export const Review = () => {
    const [formData, setFormData] = useState({
        review: "",
        rate: 0
    });
    const [showLoading, setShowLoading] = useState(false);

    const handleSubmit = () => {
        setShowLoading(true);
        createReview(formData)
            .then((result) => {
                setShowLoading(false);
                window.location.reload();


            })
            .catch((error) => {
                console.error("Error al crear ticket:", error);
                setShowLoading(false);
            });

    };

    return (
        <>
            <Loading loading={showLoading} />
            <div className="container">
                <div className="section1 d-flex justify-content-center align-items-center position-relative">
                     <Menu/>
                    <Header />
                    <ImageCarousel />
                </div>

                <div className="section4 d-flex justify-content-center align-items-center position-relative">
                    <Row>
                        <h3 className="title">GRACIAS POR SU ASISTENCIA</h3>
                        <a
                            href="https://drive.google.com/drive/folders/1M-cwpMnbhVvMJw-hoKPfMnoK1CsPAruc?usp=drive_link"
                            target="_blank"
                            rel="noopener noreferrer"
                           className="title blink"
                        >
                            Imágenes
                        </a>
                    </Row>

                </div>

                <div className="section5 d-flex justify-content-center align-items-center position-relative">
                    <Row>
                        <p className="title">
                            Nos gustaría saber tu opinión, ¿Podrías evaluarnos?
                        </p>
                    </Row>

                </div>
                <div className="section5 d-flex justify-content-center align-items-center position-relative">
                    <Row>
                        <Rate initialRating={0} onChange={(e) => setFormData({
                            ...formData,
                            rate: e
                        })} />

                    </Row>

                </div>
                <div className="section5 d-flex justify-content-center align-items-center position-relative">
                    <FormRate onChange={(e) => setFormData({
                        ...formData,
                        review: e.review
                    })} />
                </div>
                <div className="section5 d-flex justify-content-center align-items-center position-relative">
                    <Button
                        className="buttonSendRate"
                        variant="outline-warning"
                        type="submit"
                        onClick={handleSubmit}
                    >
                        Opinar
                    </Button>
                </div>

                <div className="section5 d-flex justify-content-center align-items-center position-relative">
                    <Row>
                        <p className="title">
                            ¿Quieres ayudar a financiar el evento? Envía un Bizum al organizador UwU
                        </p>
                    </Row>

                </div>

            </div>
        </>
    );
};