import { Row, Col } from "react-bootstrap";
import "./menu.scss";
import { useNavigate } from "react-router-dom";
export const Menu = () => {
      const navigate = useNavigate();
    return (
        <div className="containerMenu">
            <Row className="d-flex justify-content-end">
                <Col md="auto">
                    <span  onClick={()=>navigate('/')} className="inicio desc">INICIO</span>
                    <span onClick={()=>navigate('/rifa')} className="desc">RIFA</span>
                            <span onClick={()=>navigate('/map')} className="desc">MAPA</span>
                </Col>

            </Row>
        </div>
    );
};
